html,
body,
#root {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	height: 100%;
}
input,
textarea {
	font-family: Arial;
}
.login {
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}
.login-form {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
.login-form input {
	max-width: 100%;
	border-top: none;
	border-left: none;
	border-right: none;
	height: 20px;
	line-height: 20px;
	font-size: 20px;
	border-bottom: solid 2px #000000;
	transition: all .23s ease-in;
}
.login-form input:focus {
	border-bottom: solid 2px #000000;
	outline: none;
}
.login-form .error {
	text-align: center;
	margin: 5px 0;
	padding: 5px 10px;
	color: #000000;
}
.container {
	color: #000000;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	height: 100%;
	width: 100%;
}
#side-bar {
	box-sizing: border-box;
	width: 33.75%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
#side-bar .heading {
	box-sizing: border-box;
	height: 12vh;
	max-height: 65px;
	padding: 18px 16px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	background: #ffffff;
}
#side-bar .search {
	background: #ffffff;
	box-sizing: border-box;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	border-width: 1px 0;
	border-style: solid;
	border-color: black;
	padding-left: 15px;
	padding-right: 20px;
	height: 10vh;
	max-height: 65px;
}
#side-bar .search .search-icon {
	margin-right: 15px;
	cursor: pointer;
}
#side-bar .search input {
	width: 100%;
	background: #ffffff;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
	box-sizing: border-box;
	border: none;
	color: #ffffff;
}
#side-bar .search input:focus {
	outline: none;
}
#side-bar .search input::-webkit-input-placeholder {
	color: #ffffff;
	opacity: .6;
}
#side-bar .search input::-ms-input-placeholder {
	color: #ffffff;
	opacity: .6;
}
#side-bar .search input::placeholder {
	color: #ffffff;
	opacity: .6;
}
#side-bar .search .plus {
	display: inline-block;
	position: relative;
	cursor: pointer;
	width: 13px;
	height: 13px;
}
#side-bar .search .plus::after,
#side-bar .search .plus::before {
	content: '';
	position: absolute;
	background: #ffffff;
}
#side-bar .search .plus::after {
	width: 16px;
	height: 2px;
	top: 5px;
}
#side-bar .search .plus::before {
	width: 2px;
	height: 16px;
	top: -2px;
	left: 7px;
}
#side-bar .users {
	overflow-y: scroll;
	background: #ffffff;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}
#side-bar .users::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: #ffffff;
}
#side-bar .users::-webkit-scrollbar {
	width: 5px;
	background-color: green;
}
#side-bar .users::-webkit-scrollbar-thumb {
	background-color: #ffffff;
}
#side-bar .users .user {
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	height: 66px;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	padding: 18px 16px;
}
#side-bar .users .user.active,
#side-bar .users .user:hover {
	background: #eeeeee;
}
#side-bar .users .user:hover:not(.active) {
	cursor: pointer;
}
#side-bar .users .user .user-info {
	margin-left: 15px;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}
#side-bar .users .user .user-info .last-message {
	font-size: 12px;
	opacity: .56;
}
#side-bar .users .user .new-message {
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}
#side-bar .current-user {
	-webkit-align-items: center;
	        align-items: center;
	background: #eeeeee;
	box-sizing: border-box;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	height: 10vh;
	max-height: 65px;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	padding-left: 16px;
	padding-right: 16px;
}
#side-bar .current-user .logout {
	-webkit-align-items: center;
	        align-items: center;
	cursor: pointer;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	font-size: 2em;
}
.chat-header {
	background: #ffffff;
	box-shadow: 0px 6px 5px -2px rgba(255, 255, 255, 0.7);
	box-sizing: border-box;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	height: 12vh;
	max-height: 65px;
	padding: 18px 16px;
}
.chat-header .user-info {
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
}
.chat-header .user-info .user-name {
	margin-right: 10px;
}
.chat-header .user-info .status {
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
}
.chat-header .options {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	height: 100%;
	width: 15%;
}
.chat-header .options svg {
	cursor: pointer;
}
.chat-room-container {
	height: 100%;
	width: 76.25%;
}
@media screen and (max-width: 510px) {
	#side-bar {
		position: absolute;
		left: -100%;
	}
	.chat-room-container {
		width: 100%;
	}
}
.chat-room {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	height: 100%;
	width: 100%;
}
.chat-room.choose {
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	font-size: 2em;
}
.thread-container {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
	overflow-y: scroll;
	position: relative;
}
.thread-container::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: #ffffff;
}
.thread-container::-webkit-scrollbar {
	width: 5px;
	background-color: green;
}
.thread-container::-webkit-scrollbar-thumb {
	background-color: #ffffff;
}
.thread-container .thread {
	position: relative;
	width: 100%;
	min-height: 800px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	color: #4b4b4b;
	background: #eeeeee;
}
.message-container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	min-height: 50px;
	margin: 10px 15px;
	-webkit-animation: .65s ease-out 0s show;
	        animation: .65s ease-out 0s show;
}
.message-container .time {
	-webkit-order: 1;
	        order: 1;
}
.message-container .data {
	-webkit-order: 2;
	        order: 2;
	height: 100%;
	margin-left: 25px;
}
.message-container .name {
	font-size: .65em;
	margin-top: 5px;
	text-align: right;
}
.message-container .message {
	background: #4b4b4b;
	border-radius: 5px;
	border-top-left-radius: 0;
	box-sizing: border-box;
	color: #ffffff;
	height: 100%;
	padding: 10px 15px;
	position: relative;
}
@-webkit-keyframes show {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes show {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.message-container .message::before {
	border-bottom-color: transparent;
	border-left-color: transparent;
	border-right-color: #4b4b4b;
	border-style: solid;
	border-top-color: #4b4b4b;
	border-width: 4px;
	content: '';
	height: 0;
	left: -7px;
	position: absolute;
	top: 0;
	width: 0;
}
.message-container.right {
	text-align: right;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
}
.message-container.right .time {
	-webkit-order: 2;
	        order: 2;
	margin-left: 25px;
}
.message-container.right .data {
	margin-left: 0;
	-webkit-order: 1;
	        order: 1;
}
.message-container.right .name {
	display: none;
}
.message-container.right .message {
	background: #8f8f8f;
	color: #fff;
	border-top-right-radius: 0;
	border-top-left-radius: 5px;
}
.message-container.right .message::before {
	border-top-color: #8f8f8f;
	border-left-color: #8f8f8f;
	border-right-color: transparent;
	left: auto;
	right: -7px;
}
.typing-user {
	text-align: right;
	margin: 10px 15px;
}
.message-input {
	background: white;
	color: #484d79;
	box-sizing: border-box;
	height: 10vh;
	max-height: 65px;
}
.message-input .message-form {
	height: 100%;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.message-input .message-form .form-control {
	padding-top: 24px;
	padding-bottom: 24px;
	resize: none;
	padding-left: 15px;
	box-sizing: border-box;
	width: 80%;
	height: 100%;
	border: none;
}
.message-input .message-form .form-control::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: #ffffff;
}
.message-input .message-form .form-control::-webkit-scrollbar {
	width: 5px;
	background-color: green;
}
.message-input .message-form .form-control::-webkit-scrollbar-thumb {
	background-color: #696969;
}
.message-input .message-form .form-control:focus {
	outline: none;
}
.message-input .message-form .send {
	width: 20%;
	box-sizing: border-box;
	font-size: 1.25em;
	text-align: center;
	border: none;
	height: 100%;
	color: #fff;
	background: #8f8f8f;
	transition: all .35s ease-out;
}
.message-input .message-form .send:disabled {
	opacity: .2;
	background: #0e0e0e;
}

